import React,{useEffect,useState} from 'react';
import { StyleSheet, Text, View , ActivityIndicator , Button } from 'react-native';
import randomQuote from 'random-quotes';

export default function App() {
  const [quotes, setQuotes] = useState(null);//'Open up App.js to start working on your app!')
  useEffect(() => {
    getQute();
  }, []);
  const getQute = ()=>{
    setQuotes(randomQuote(1));
  }
  return (
    <View style={styles.container}>
      {quotes ? (
        <>
        <Text style={{fontSize:18 , fontWeight:'200'}}>{quotes.body}</Text>
        <Text style={{fontSize:24 , fontWeight:'800' , margin:20}}>{quotes.author}</Text>
        <Button title='New Quote' onPress={getQute}/>
      </>
      )
       : 
        <ActivityIndicator size='large' color='#0000ff'/>
      }
        </View>
        );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
    alignItems: 'center',
    justifyContent: 'center',
  },
});
